html {
  min-height: 100%
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  /* background: url(/img/mario-bg.png) no-repeat; */

  background-size: 100%;
  background-position: bottom;
  background-color: mediumslateblue;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  min-height: 100%;
}


form {
  padding: 20px;
  margin-top: 60px;
}

form button,
form h5 {
  padding: 20px 0;
}

input[type=text]:not(.browser-default):focus:not([readonly]) input[type=email]:not(.browser-default):focus:not([readonly]) input[type=password]:not(.browser-default):focus:not([readonly]) textarea.materialize-textarea:focus:not([readonly]) {
  border-color: teal;
  box-shadow: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
